import React, { Component } from "react";
import M from "materialize-css";
import './graph.css';
import PriceHistoryChart from './PriceHistoryChart';
import BitcoinService from './bitcoinService';
import axios from 'axios';

class GraphModal extends Component {
  constructor(props) {
		super(props);
		this.state = {
      chartData:null
    }
  }

  componentDidMount() {
    const options = {
      onOpenStart: () => {
        console.log("Open Start");
      },
      onOpenEnd: () => {
        console.log("Open End");
      },
      onCloseStart: () => {
        console.log("Close Start");
      },
      onCloseEnd: () => {
        console.log("Close End");
      },
      inDuration: 250,
      outDuration: 250,
      opacity: 0.5,
      dismissible: false,
      startingTop: "4%",
      endingTop: "10%"
    };
    M.Modal.init(this.Modal, options);
    // If you want to work on instance of the Modal then you can use the below code snippet 
    // let instance = M.Modal.getInstance(this.Modal);
    // instance.open();
    // instance.close();
    // instance.destroy();
    this.getPricesByTicker(this.props.ticker);
  }

  getPricesByTicker = (ticker) => {
    axios.get('https://min-api.cryptocompare.com/data/histominute?fsym=' + ticker + '&tsym=USD&limit=14&aggregate=15&e=CCCAGG')
			.then(response => {
				var prices = response.data;
        this.setState({ chartData: prices.Data });
			})
			.catch(err => console.log(err));
  }

  render() {
    let prices = []
    if(this.state.chartData ==  null) {
      let data = [{
        x: new Date(0),
        y: [0, 0, 0, 0]
      },
      {
        x: new Date(1),
        y: [0,0,0,0]
      }
      ]
      prices.push({data});
    }
    else {
      let data = [];
			this.state.chartData.forEach(function(x){
				var obj = {};
				obj.x = new Date(x.time * 1000).toLocaleString();
				obj.y = [x.open, x.high, x.low, x.close];
				data.push(obj);
      });
      prices.push({data});
    }
    let clazz = this.props.ticker + " modal-content";
    let modalClazz = this.props.ticker + " modal";
    return (
      <>
        <div className="right-align">
        <a
          className="graph modal-trigger"
          data-target={modalClazz}
        >
            <i class="material-icons Small">insert_chart</i>

        </a>
        </div>
        <div
          ref={Modal => {
            this.Modal = Modal;
          }}
          id={modalClazz}
          className={modalClazz}
        >
          {/* If you want Bottom Sheet Modal then add 
        bottom-sheet class */}
          <div className={clazz}>
            <h4>{this.props.name} Data</h4>
            <PriceHistoryChart className={clazz} ticker={this.props.ticker}/>
          </div>
          <div class="modal-footer">
            <a href="#" class="modal-close waves-effect waves-grey btn-flat">
              Close
            </a>
          </div>
        </div>
      </>
    );
  }
}

export default GraphModal;
